@import url(https://fonts.googleapis.com/css?family=Overpass);
.parent-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
  border:2px solid black;
  height:500px;
  margin:40px;
  width:70%;
  position: relative;
  padding: 2px;
}
.editorContainer{
  border:5px solid red;
  width:50%;
  max-width: 50%;
  top:0;
  bottom: 0;
  left:0;
  position: absolute;
 
}

.toolbar-container{
     
}
.small-btn{
    margin-left: 450px;
    margin-top: -45px;
    height: 30px;
    padding: 5px;
    width: 200px;
    display: flex;
    justify-content: end;
    
}

.bttn{
  background-color: #cad1ca;
  border: none;
  border-radius: 5px;
  color: black;
  padding: 5px;
  margin:5px;
  font-size: 12px;
  cursor: pointer;
  
}
.sideToolbar{
    width:25%;
    top:0;
    bottom: 0;
    right:0;
    position: absolute;
    font-family: 'Inter', sans-serif;
  }
.performance-container{
    margin:8px;
    margin-bottom: 8px;
    font-weight: 400;
    
}

.feature-boxes{
    border: 1px solid rgb(236, 239, 241);
    box-sizing: border-box;
    border-radius: 4px;
}

.box1{
    color:rgb(46, 44, 44);
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25px;
    padding:1px 0px 12px 15px;
    box-shadow: rgb(236, 239, 241) 0px -1px 0px inset;
}
.addons-container{
    margin:8px;
}

.addOn-boxes{
    border: 1px solid rgb(236, 239, 241);
    box-sizing: border-box;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box-1{
    color:rgb(46, 44, 44);
    font-weight: normal;
    line-height: 1.25px;
    padding:1px 0px 12px 15px;
    box-shadow: rgb(236, 239, 241) 0px -1px 0px inset;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}

.fc{
    background-color: rgb(46, 46, 165);
    height:35px;
    width:120px;
    border-radius: 10px;
    text-align: center;
    vertical-align: middle;
    line-height: 30px; 
    color:white;
    font-size: 13px;
}
.keywords-container{
    margin-left:8px;
    bottom:0;
}
.tags-input {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	min-height: 48px;
    ;
	width: 12rem;
	padding: 0 8px;
	border: 1px solid rgb(214, 216, 218);
	border-radius: 6px;
}

input {
    flex: 1 1;
    border: none;
    height: 36px;
    font-size: 14px;
    padding: 4px 0 0 0;
    outline: transparent;
    
}
#tags {
 
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	margin: 8px 0 0 0;
}

.tag {
	width: auto;
	height: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	padding: 0 8px;
	font-size: 10px;
	list-style: none;
	border-radius: 6px;
	margin: 0 6px 6px 0;
    background: skyblue;
}
.tag-title {
    margin-top: 3px;
}

.tag-close-icon {
    display: block;
    width: 12px;
    height: 12px;
    line-height: 12px;
    text-align: center;
    font-size: 14px;
    margin-left: 5px;
    color: #0052cc;
    border-radius: 50%;
    background: #fff;
    cursor: pointer;
}
@media screen and (max-width: 567px) {
	.tags-input {
		width: calc(100vw - 32px);
	}
}

